.page {
  font-family: "Poppins", Arial, Helvetica, sans-serif;
  scroll-behavior: smooth;
  color: $main-text-color;

  &__body {
    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }
}
